import React, { useState, useEffect } from 'react';

function Body() {
    const [loading, setLoading] = useState(true);
    const [comments, setComments] = useState(null);
    const [deleting, setDeleting] = useState(false);
    useEffect(() => {
        fetch('https://jsonplaceholder.typicode.com/comments')
        .then((response) => response.json())
        .then((json) => {
            setComments(json);
            setLoading(false)
        })
    }, []);

    const delComment = (id) => {
        setDeleting(true);
        fetch(`https://jsonplaceholder.typicode.com/comments/${id}`, {
            method: 'DELETE',
        })
        .then((response) => response.json())
        .then((json) => {
            setDeleting(false);
            setComments(comments.filter((item) => item.id !== id));
        })
    }
    
    return(
        <div className='commentsbox'>
            {loading? 
            <i className = "loading fa fa-spinner fa-spin" aria-hidden = "true"></i> : (
                 comments.map((comment) => {
                     return(
                         <div className = 'comment'>
                             <div className = 'email'>
                                 {comment.email}
                             </div>
                            <div className = 'text'>
                                {comment.body} 
                                <button className = 'button' disabled={deleting} onClick={() => delComment(comment.id)}>
                                    {deleting? 
                                    <i className = "deleting fa fa-cog fa-spin" aria-hidden="true"></i> :
                                    <i class="fa fa-trash" aria-hidden="true"></i>}
                                </button>
                            </div>
                         </div>
                     )
                 })
            )}
        </div>
    )
}

export default Body;